@mixin margin-left-out($current_cols, $add-left-cols, $gutter) {
    margin-left: calc( (100% + #{$gutter} * 2) / -#{$current_cols} * #{$add-left-cols} - #{$gutter});
}

@mixin margin-right-out($current_cols, $add-right-cols, $gutter) {
    margin-right: calc( (100% + #{$gutter} * 2) / -#{$current_cols} * #{$add-right-cols} - #{$gutter});
}

@mixin full-width {
    @include media-breakpoint-up(lg) {
        @include margin-left-out(7, 2, 15px);
        @include margin-right-out(7, 3, 15px);
    }
}



